*, *::before, *::after{
   box-sizing: border-box;
  }
  
*{
   margin: 0;
 }

body{
	font-family: sans-serif;
	background: #252525;
}

.home{
	margin-top: 3rem;
}
.container{
   padding: 0 1rem;
}

.header{
	padding-top: 2rem;
    background: #252525;
	font-size: 1.45rem;
	color: white;
	position: fixed;
	top: 0;
	z-index: 1;
	width: 100%;
}
.search{
	background: #E5E5E5;
	border: none;
	outline: 0;
	border-radius: 5px;
	width: 100%;
	padding: 0.25rem 0.25rem;
	font-size: 1.25rem;
}